<template>
    <div class="bi-content">
        <div class="bi-type">
            <div class="bi-type-li">
                <img src="./../assets/img/icon/bi-icon-list.png" alt="" class="bi-type-li-icon">
                线索
            </div>
            <div class="bi-type-li">
                <img src="./../assets/img/icon/bi-icon-list.png" alt="" class="bi-type-li-icon">
                流量
            </div>
            <div class="bi-type-li">
                <img src="./../assets/img/icon/bi-icon-list.png" alt="" class="bi-type-li-icon">
                厂商
            </div>
            <div class="bi-type-li">
                <img src="./../assets/img/icon/bi-icon-list.png" alt="" class="bi-type-li-icon">
                会员
            </div>
        </div>
        <div class="bi-body">
            <div class="bi-bli">
                <div class="bi-bli-title">
                    <img :src="iconsMap['0']" alt="" class="bi-bli-icon">
                    <div class="bi-bli-text">线索数据</div>
                </div>
                <div class="bi-bli-outer">
                    <div class="bi-bl-wrap">
                        <div class="bi-bl-desc">
                            线索量走势图
                            <div class="bi-bl-full" @click="toFullScreen(1)"></div>
                        </div>
                        <div class="bi-bl-tabs">
                            <div class="bi-bt-inner">
                                <div v-for="(item, index) in list['1']['dy']"
                                     :class="{'bi-bt-item-active': index === list['1']['index'] }"
                                     @click="changeDataFirst('1', index)"
                                     class="bi-bt-item">{{ item.name }}</div>
                            </div>
                        </div>
                        <div class="bi-charts-inner">
                            <div id="chart-global-1" class="bi-chart bi-chart-1"></div>
                        </div>
                    </div>
                    <div class="bi-bl-wrap">
                        <div class="bi-bl-desc">
                            线索量及月活用户走势图
                            <div class="bi-bl-full"></div>
                        </div>
                        <div class="bi-bl-tabs">
                            <div class="bi-bt-inner">
                                <div v-for="(item, index) in list['2']['legend']"
                                     :class="{'bi-bt-item-active': index === list['2']['index'] }"
                                     @click="changeDataSecond('2', index)"
                                     class="bi-bt-item">{{ item }}</div>
                            </div>
                        </div>
                        <div class="bi-charts-inner">
                            <div id="chart-global-2" class="bi-chart bi-chart-2"></div>
                        </div>
                    </div>
                </div>
            </div>

            <div class="bi-bli">
                <div class="bi-bli-title">
                    <img :src="iconsMap['1']" alt="" class="bi-bli-icon">
                    <div class="bi-bli-text">流量数据</div>
                </div>
                <div class="bi-bli-outer">
                    <div class="bi-bl-wrap">
                        <div class="bi-bl-desc">
                            日活用户走势图
                            <div class="bi-bl-full"></div>
                        </div>
                        <div class="bi-bl-tabs">
                            <div class="bi-bt-inner">
                                <div v-for="(item, index) in list['3']['legend']"
                                     :class="{'bi-bt-item-active': index === list['3']['index'] }"
                                     @click="changeDataThird('3', index)"
                                     class="bi-bt-item">{{ item }}</div>
                            </div>
                        </div>
                        <div class="bi-charts-inner">
                            <div id="chart-global-3" class="bi-chart bi-chart-3"></div>
                        </div>
                    </div>
                    <div class="bi-bl-wrap">
                        <div class="bi-bl-desc">
                            新增用户走势图
                            <div class="bi-bl-full"></div>
                        </div>
                        <div class="bi-charts-inner">
                            <div id="chart-global-4" class="bi-chart bi-chart-4"></div>
                        </div>
                    </div>
                    <div class="bi-bl-wrap">
                        <div class="bi-bl-desc">
                            总浏览量
                            <div class="bi-bl-full"></div>
                        </div>
                        <div class="bi-bl-tabs">
                            <div class="bi-bt-inner">
                                <div v-for="(item, index) in list['5']['name']"
                                     :class="{'bi-bt-item-active': index === list['5']['index'] }"
                                     @click="changeDataFifth('5', index)"
                                     class="bi-bt-item">{{ item }}</div>
                            </div>
                        </div>
                        <div class="bi-charts-inner">
                            <div id="chart-global-5" class="bi-chart bi-chart-5"></div>
                        </div>
                    </div>
                </div>
            </div>

            <div class="bi-bli">
                <div class="bi-bli-title">
                    <img :src="iconsMap['2']" alt="" class="bi-bli-icon">
                    <div class="bi-bli-text">品牌厂商</div>
                </div>
                <div class="bi-bli-outer">
                    <div class="bi-bl-wrap">
                        <div class="bi-bl-desc">
                            各大区品牌覆盖情况
                            <div class="bi-bl-full"></div>
                        </div>
                        <div class="bi-charts-inner">
                            <div id="chart-global-6" class="bi-chart bi-chart-6"></div>
                        </div>
                    </div>
                </div>
            </div>

            <div class="bi-bli">
                <div class="bi-bli-title">
                    <img :src="iconsMap['3']" alt="" class="bi-bli-icon">
                    <div class="bi-bli-text">会员</div>
                </div>
                <div class="bi-bli-outer">
                    <div class="bi-bl-wrap">
                        <div class="bi-bl-desc">
                            全国经销商签约数量
                            <div class="bi-bl-full"></div>
                        </div>
                        <div class="bi-charts-inner">
                            <div id="chart-global-7" class="bi-chart bi-chart-7"></div>
                        </div>
                    </div>
                    <div class="bi-bl-wrap">
                        <div class="bi-bl-desc">
                            全国经销商覆盖情况
                            <div class="bi-bl-full"></div>
                        </div>
                        <div class="bi-charts-inner">
                            <div id="chart-global-8" class="bi-chart bi-chart-8"></div>
                        </div>
                    </div>
                </div>
            </div>

        </div>
    </div>
</template>

<script>
    require('echarts/map/js/china.js')
    import { GlobalData } from './../data'
    import { IconsMap, ColorsMap } from './../common/constants'
    // import Utils from './../common/utils'
    // import Bus from './../common/bus'

    import { Toast } from 'vant';

    export default {
        components: {},
        data() {
            return {
                index: 0,
                html: '',
                data: GlobalData,
                charts: GlobalData.result.data.groups[0].charts,
                ol: GlobalData.result.data.groups[0].charts.map(item=>item.data),
                iconsMap: IconsMap,
                colorsMap: '',
                dividerColor: ColorsMap.dividerColor,
                fontColor: ColorsMap.fontColor,
                lineStyle: {
                    lineStyle: {
                        color: ColorsMap.dividerColor,
                        width: 1,
                        type: 'solid'
                    },
                },
                textStyle: {
                    textStyle: {
                        color: ColorsMap.fontColor,
                    },
                },
                linesStyle: {
                    lineStyle: {
                        // color: '#62B7F1',
                        width: 1,
                    }
                },
                list: {
                    1: {
                        dy: [],
                        index: 0,
                    },
                    2: {
                        legend: [],
                        index: 0,
                    },
                    3: {
                        legend: [],
                        index: 0,
                    },
                    5: {
                        name: [],
                        index: 0,
                    },
                },
                chartOpts: {},
                chartObjs: {},
                els: [
                    'chart-global-1',
                    'chart-global-2',
                    'chart-global-3',
                    'chart-global-4',
                    'chart-global-5',
                    'chart-global-6',
                    'chart-global-7',
                    'chart-global-8',
                ],
                tabIndex: 1,
                keysMap: {
                    detailData: 'bi-detail-local-data',
                },
            }
        },
        created(){
            this.setOptions()
        },
        mounted() {
            this.getChartsObj()
            this.initCharts()

            this.getChartsData()
            // console.log(this.charts)
            // this.getData()
            // this.charts = res.result.data.groups[0].charts
            // this.ol = this.charts.map(item=>item.data)
            // console.log(this.ol)

            // this.drawCharts()
            // this.setChartOpts()

        },
        methods: {
            getChartsData(){
                let itemToast = Toast.loading({
                    duration: 0,       // 持续展示 toast
                    forbidClick: true, // 禁用背景点击
                    loadingType: 'spinner',
                    message: '数据加载中...'
                });
                this.$http.ajax({
                    url: '/api/v2/all/8',
                    data: {}
                }).then(res=>{
                    itemToast.clear()
                    console.log(res)
                    this.charts = res.result.data.groups[0].charts
                    this.ol = this.charts.map(item=>item.data)
                    // console.log(this.ol)
                    this.drawCharts()
                    this.setChartOpts()
                }).catch(e=>{
                    console.log(e)
                })
            },
            toFullScreen(id){
                let data = JSON.stringify(this.list[id])
                let index = this.list[id]['index']
                index = /^\d+(\.\d+)?$/.test(index)?index:100
                window.localStorage.setItem(this.keysMap.detailData, data)
                this.$router.push({
                    path: '/report/detail',
                    query: {
                        id: id,
                        td: this.tabIndex,
                        index: index,
                    }
                })
            },
            drawCharts() {

                this.dataHandle('1', ()=>{
                    let s1 = JSON.parse(this.ol[1][0].data)
                    return {
                        index: 0,
                        unit: [
                            s1.leftUnit,
                        ],
                        dx: s1.xAxis,
                        dy: [
                            {
                                name: '总概览',
                                value: s1.yAxis,
                            },
                            ...s1.subData,
                        ],
                        rawData: s1,
                    }
                })

                this.dataHandle('2', ()=>{
                    let s2 = JSON.parse(this.ol[1][1].data)
                    let { bars, lines, summary } = s2
                    console.log(s2)
                    let barsData = []
                    bars.map((item,i)=>{
                        let obj = {
                            name: item.name,
                            type: 'bar',
                            stack: '线索',
                            data: item.value.map(val=>val/10000),
                            barWidth: 10,
                        }
                        if(i===bars.length-1){
                            obj = Object.assign({},obj, {
                                itemStyle: {
                                    barBorderRadius: [7,7,0,0]
                                },
                            })
                        }
                        barsData.push(obj)
                    })
                    barsData.push({
                        name: '月活用户',
                        type: 'line',
                        data: lines[0].value.map(val=>val/10000),
                        yAxisIndex: 1,
                        symbol: 'none',
                        ...this.linesStyle,
                    })
                    return {
                        index: 0,
                        legend: [
                            '总概览',
                            ...bars.map(item=>item.name),
                        ],
                        name: [
                            ...bars.map(item=>item.name),
                            lines[0].name,
                        ],
                        summary: summary,
                        dx: s2.xAxis,
                        dy: barsData,
                        bars,
                        lines: [
                            // {
                            //     name: '总概况',
                            //     value: lines[0].value
                            // },
                            ...lines[0].subData,
                        ],
                        xAFull: s2.xAxisFull,
                        rawData: s2,
                    }
                })
                
                this.dataHandle('3', ()=>{
                    let s3 = JSON.parse(this.ol[2][0].data)
                    // console.log(s3)
                    let dy = []
                    let name = []
                    s3.yAxis.map((item, i)=>{
                        name.push(item.name)
                        let obj = {
                            name: item.name,
                            type: 'bar',
                            stack: '线索',
                            data: item.value.map(val=>val/10000),
                            barWidth: 10,
                        }
                        if(i===(s3.yAxis.length-1)){
                            obj = Object.assign({},obj, {
                                itemStyle: {
                                    barBorderRadius: [5,5,0,0]
                                },
                            })
                        }
                        dy.push(obj)
                    })

                    return {
                        index: 0,
                        legend: [
                            '总概览',
                            ...name,
                        ],
                        name,
                        dx: s3.xAxis,
                        dy,
                        bars: s3.yAxis,
                        summary: s3.summary,
                        rawData: s3,
                    }
                })

                this.dataHandle('4', ()=> {
                    let s4 = JSON.parse(this.ol[2][1].data)
                    console.log(s4)
                    let dx = s4.xAxis
                    let name = []
                    let dy = []
                    s4.bars.map((item,i)=>{
                        name.push(item.name)
                        let obj = {
                            name: item.name,
                            type: 'bar',
                            stack: '线索',
                            data: item.value.map(val=>val/10000),
                            barWidth: 10,
                        }
                        if(i===(s4.bars.length-1)){
                            obj = Object.assign({},obj, {
                                itemStyle: {
                                    barBorderRadius: [5,5,0,0]
                                },
                            })
                        }
                        dy.push(obj)
                    })
                    s4.lines.map(item=>{
                        name.push(item.name)
                        dy.push({
                            name: item.name,
                            type: 'line',
                            data: item.value.map(val=>val/10000),
                            yAxisIndex: 1,
                            symbol: 'none',
                            ...this.linesStyle,
                        })
                    })

                    return {
                        name,
                        dx,
                        dy,
                        rawData: s4,
                    }
                })

                this.dataHandle('5', ()=> {
                    let s5 = JSON.parse(this.ol[2][2].data)
                    console.log(s5)
                    let dx = s5.xaxis
                    let legend = []
                    let dy = []
                    s5.yaxis.map(item=>{
                        legend.push(item.name)
                        dy.push({
                            name: item.name,
                            type: 'line',
                            stack: '总浏览量',
                            areaStyle: {},
                            data: item.value.map(val=>val/10000),
                            symbol: 'none',
                        })
                    })
                    dy[0].lineStyle = {
                        normal: {
                            color: '#06D9B2',
                            shadowColor: '#06D9B2',
                            shadowBlur: 10,
                            width: 1,
                        }
                    }
                    dy[1].lineStyle = {
                        normal: {
                            color: '#F5DD4D',
                                shadowColor: '#F5DD4D',
                                shadowBlur: 10,
                                width: 1,
                        }
                    }

                    return {
                        index: 0,
                        legend,
                        name: [
                            '总概览',
                            ...legend,
                        ],
                        dx,
                        dy,
                        lines: s5.yaxis,
                        rawData: s5,
                    }

                })

                this.dataHandle('6', ()=> {
                    let d6 = JSON.parse(this.ol[3][0].data).bars
                    let d6leg = d6[0].items.map(item=>item.barItemName)
                    let d6y = []
                    let d6Data = [[],[],[]]

                    d6.map(item=>{
                        d6y.unshift(item.name)
                        d6Data[0].unshift(item.items[0])
                        d6Data[1].unshift(item.items[1])
                        d6Data[2].unshift(item.items[2])
                    })
                    return {
                        legend: d6leg,
                        dx: d6y,
                        dy: d6Data,
                        rawData: d6,
                    }
                })

                this.dataHandle('7', ()=> {
                    let d7 = JSON.parse(this.ol[4][0].data).provinces
                    let d7Data = []
                    d7.map(item=>{
                        d7Data.push({
                            name: item.name,
                            value: item.percent,
                            itemStyle: {
                                borderColor: '#6A9CC6',
                                borderWidth: 1,
                            },
                        })
                    })
                    return {
                        data: d7Data,
                        rawData: d7,
                    }
                })

                this.dataHandle('8', ()=> {
                    let d8 = JSON.parse(this.ol[4][1].data).provinces
                    let d8Data = []
                    d8.map(item=>{
                        d8Data.push({
                            name: item.name,
                            value: item.percent,
                            itemStyle: {
                                borderColor: '#6A9CC6',
                                borderWidth: 1,
                            },
                        })
                    })
                    let d8t = JSON.parse(this.ol[4][2].data)[0]
                    return {
                        data: d8Data,
                        title: d8t,
                        rawData: d8,
                    }
                })

            },
            initCharts(){
                for (let i=1;i < this.els.length+1; i++){
                    let key = i+''
                    this.chartObjs[key].setOption(this.chartOpts[key])
                }
            },
            setChartOpts(){

                this.setChartData('1', (chart, item)=>{
                    chart.setOption({
                        xAxis: [
                            {
                                data: item.dx,
                            },
                        ],
                        series: [
                            {
                                data: item.dy[0].value,
                            }
                        ],
                    })
                })

                this.setChartDataCommon('2')
                this.setChartDataCommon('3')
                this.setChartDataCommon('4')
                this.setChartDataCommon('5')

                this.setChartData('6', (chart, item)=>{
                    chart.setOption({
                        legend: {
                            data: item.legend,
                        },
                        yAxis: [
                            {
                                data: item.dx,
                            },
                        ],
                        series: [
                            {
                                name: item['legend'][1],
                                type:'bar',
                                label: {
                                    normal: {
                                        show: true,
                                        position: 'right',
                                        formatter: params=>{
                                            return `${item['dy'][1][params.dataIndex].showValue}`
                                        },
                                    },
                                },
                                itemStyle: {
                                    barBorderRadius: [0,7,7,0]
                                },
                                barWidth: 17,
                                data: item['dy'][1].map(item=>item.percent),
                            },
                            {
                                name: item['legend'][2],
                                type:'bar',
                                stack: '总量',
                                label: {
                                    normal: {
                                        show: true,
                                        position: 'right',
                                        formatter: params=>{
                                            return `${item['dy'][2][params.dataIndex].showValue}`
                                        },
                                    },
                                },
                                itemStyle: {
                                    barBorderRadius: [0,7,7,0]
                                },
                                barWidth: 17,
                                data: item['dy'][2].map(item=>item.percent),
                            },
                            {
                                name: item['legend'][0],
                                type:'bar',
                                stack: '总量',
                                // barWidth: 100,
                                label: {
                                    normal: {
                                        show: true,
                                        position: 'insideLeft',
                                        color: '#fff',
                                        formatter: params=>{
                                            return `${item['dy'][0][params.dataIndex].showValue}`
                                        },
                                        // fontSize: 10,
                                    },
                                },
                                itemStyle: {
                                    barBorderRadius: [7,0,0,7],
                                },
                                barWidth: 17,
                                data: item['dy'][0].map(item=>-item.percent),
                            },
                        ]
                    })
                })

                this.setChartData('7', (chart, item)=>{
                    chart.setOption({
                        series: [
                            {
                                data: item.data,
                            }
                        ],
                    })
                })

                this.setChartData('8', (chart, item)=>{
                    chart.setOption({
                        title: {
                            text: item.title,
                        },
                        series: [
                            {
                                data: item.data,
                            }
                        ],
                    })
                })

            },
            setChartDataCommon(id){
                let chart =  this.chartObjs[id]
                let item = this.list[id]
                chart.setOption({
                    legend: {
                        data: item.name,
                    },
                    xAxis: [
                        {
                            data: item.dx,
                        },
                    ],
                    series: item.dy,
                })
            },
            setChartData(id, cb){
                let chart =  this.chartObjs[id]
                let item = this.list[id]
                cb && cb(chart, item)
            },
            getChartsObj(){
                // echarts 实例化
                this.els.map((item, i)=>{
                    this.chartObjs[i+1+''] = this.$echarts.init(document.getElementById(item))
                })
            },
            dataHandle(id, cb){
                this.list[id] = cb()
            },
            changeDataFirst(n,index){
                let item = this.list[n]
                item.index = index
                this.chartObjs[n].setOption({
                    series: [
                        {
                            data: item.dy[index].value,
                        }
                    ],
                })
            },
            changeDataSecond(n, index){
                let item = this.list[n]
                item.index = index
                let charts = this.chartObjs[n]
                charts.clear()
                charts.setOption(this.chartOpts[n])
                // console.log(this.list[n].dy[index].value)
                if (index===0) {
                    charts.setOption({
                        legend: {
                            data: item.name,
                        },
                        xAxis: [
                            {
                                data: item.dx,
                            },
                        ],
                        series: item.dy,
                    })
                } else {
                    index = index - 1
                    let legend = [
                        item.bars[index].name,
                        '总线索',
                        '月活用户',
                    ]
                    let obj = {
                        type: 'bar',
                        stack: '线索',
                        barWidth: 10,
                    }
                    let dy = [
                        Object.assign({},obj, {
                            name: item.bars[index].name,
                            data: item.bars[index].value.map(val=>val/10000),
                        }),
                        Object.assign({},obj, {
                            name: '总线索',
                            data: item.summary.value.map(val=>val/10000),
                            itemStyle: {
                                barBorderRadius: [7,7,0,0]
                            },
                        }),
                        {
                            name: '月活用户',
                            type: 'line',
                            data: item.lines[index].value.map(val=>val/10000),
                            yAxisIndex: 1,
                            symbol: 'none',
                            ...this.linesStyle,
                        }
                    ]

                    charts.setOption({
                        grid: {
                          bottom: 50,
                        },
                        legend: {
                            data: legend,
                        },
                        xAxis: [
                            {
                                data: item.dx,
                            },
                        ],
                        series: dy,
                    })
                }

            },
            changeDataThird(n, index){
                let item = this.list[n]
                item.index = index
                let charts = this.chartObjs[n]
                charts.clear()
                charts.setOption(this.chartOpts[n])
                // console.log(this.list[n].dy[index].value)
                if (index===0) {
                    charts.setOption({
                        legend: {
                            data: item.name,
                        },
                        xAxis: [
                            {
                                data: item.dx,
                            },
                        ],
                        series: item.dy,
                    })
                } else {
                    index = index - 1
                    let legend = [
                        item.bars[index].name,
                        '总日活用户',
                    ]
                    let obj = {
                        type: 'bar',
                        stack: '流量',
                        barWidth: 10,
                    }
                    let dy = [
                        Object.assign({},obj, {
                            name: item.bars[index].name,
                            data: item.bars[index].value.map(val=>val/10000),
                        }),
                        Object.assign({},obj, {
                            name: '总日活用户',
                            data: item.summary.value.map(val=>val/10000),
                            itemStyle: {
                                barBorderRadius: [7,7,0,0]
                            },
                        }),
                    ]

                    charts.setOption({
                        grid: {
                            bottom: 60,
                        },
                        legend: {
                            data: legend,
                        },
                        xAxis: [
                            {
                                data: item.dx,
                            },
                        ],
                        series: dy,
                    })
                }
            },
            changeDataFifth(n, index){
                let item = this.list[n]
                item.index = index
                let charts = this.chartObjs[n]
                charts.clear()
                charts.setOption(this.chartOpts[n])
                if (index===0) {
